<template>
  <div class="terDetail">
    <p class="terDetail_p">用户详情</p>
    <div class="info_content">
      <section class="header">
        <img :src="avatarUrl" alt="暂无图片消息" class="img"/>
        <span>nickname</span>
        <p><span>性别：</span><span>{{gender}}</span></p>
      </section>
      <section class="city">
        <p><span>国家：</span><span>{{country}}</span></p>
        <p><span>省份：</span><span>{{province}}</span></p>
        <p><span>城市：</span><span>{{city}}</span></p>
      </section>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        is_forbidden:'',
        create_time:'',
        username:'',
        avatarUrl:'',
        city:'',
        gender:'',
        province:'',
        country:'',

      }
    },
    methods: {
      getUserDetail() {
        this.post("system/Info/getUserById", {
          user_id: this.$route.params.id
        }).then(res => {
          console.log(res.data)
          if(res.data.code === 10000) {
            this.nickname = res.data.data.nickName;
            this.create_time = new Date( res.data.data.create_time * 1000).toLocaleString();
            this.avatarUrl = res.data.data.avatarUrl;
            this.city = res.data.data.city;
            this.province = res.data.data.province
            this.country = res.data.data.country
             if(res.data.data.gender === "0"){
                this.gender = "男"
              }else{
                this.gender = "女"
              }
            if(res.data.data.is_forbidden === 0){
              this.is_forbidden = "启用"
            }else{
              this.is_forbidden = "禁用"
            }

          } else {
            this.data = "暂无数据"
            const content = res.data.msg
            this.$Modal.error({
              title: "获取失败",
              content: content
            });
          }
        });
      },

    },
    created() {
      console.log(this.$route.params.id)
      this.getUserDetail()
    },
  }
</script>

<style scoped>
  .terDetail_p {
    font-size: 26px;
  }
  .info_content{
    padding: 20px;
  }
  .info_content p>span:nth-child(1){
    color: #999999;
    position: relative;
  }
  .img{
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }
  .header{
    display: flex;
    align-items: center;
  }
  .header>span{
    display: inline-block;
    padding: 0 10px;
  }
  .header>p{
    padding: 0 10px;
  }
  .city{
    padding: 20px 0;
  }
  .city p{
    padding: 10px 0;
  }
</style>



